*{
    background-image: linear-gradient(to right, #00b4db, #0083b0);
    box-sizing: border-box;
}
#img{
    width: 30px;
    height: 30px;
    border: none;
   
}
#TwoPlayerModeDIV,#MulPlayerModeDIV,#SinglePlayerModeDIV{
    visibility: hidden;
    display: none;
}
#modeSelector{
    box-shadow: 2px 2px #888888;
    text-align: center;
height: 450px;
width: 400px;



background-color: transparent;

border: 1px solid black;
border-radius: 25px;
position: absolute;
top: 50%;
margin-left: 50%;
margin-right: 50%;
transform: translate(-50%,-50%);
}

#mcc{
  
}

#ptag{
    font-family: Arial, Helvetica, sans-serif;
    font-weight: 100;
left: 40px;
visibility: hidden;
position: absolute;
font-size: 10px;
color: red;
top: -2px;
cursor: pointer;
}
#multiplayer{
    
}
#twoplayer:active{
    border: 1px solid red;
}
#singleplayer:active{
    border: 1px solid red;
}

#multiplayer{
    opacity: 0.5;
    height: 25%;
    margin-bottom: 3px;
    border-radius: 5px ;
    border: 1px solid black;
    width: 50%;
    color: #000000;
    background-color:rgb(14, 14, 10) ;
    margin-left: 50%;
    margin-right: 50%;
    transform: translate(-50%,0);
}

#singleplayer{
    cursor: pointer;
    height: 25%;
    margin-bottom: 3px;
    border-radius: 5px ;
    border: 1px solid black;
    width: 50%;
    background-color:rgb(14, 14, 14) ;
    margin-left: 50%;
    margin-right: 50%;
    transform: translate(-50%,0);
}
#twoplayer{
    cursor: pointer;
    height: 25%;
    margin-bottom: 3px;
    border-radius: 5px ;
    border: 1px solid black;
    width: 50%;
    background-color:rgb(14, 14, 14) ;
    margin-left: 50%;
    margin-right: 50%;
    transform: translate(-50%,0);
}
#winnername,#winnername2{
    background-color:rgb(14, 14, 14) ;
    font-size: 25px;
    color: white;
    font-family: Arial, Helvetica, sans-serif;
    padding-bottom: 3px;
    border-bottom: 1px solid #adadad;
}
#ab{
    margin-top: -15%;
    margin-left: 15%;
   
    font-weight: 300;
    font-family: Arial, Helvetica, sans-serif;
    color: black;
    border: none;
}
.aa{
    color: #adadad;
}
#menu{
    position: absolute;
    margin-top: 50px;
    border:1px solid red;
    border-radius: 15px;
    background-color: red;
    color: white;
    font-size: 15px;
    cursor: pointer;
    left: 50%;
    transform: translateX(-50%);
    width: 90px;
    height: 30px;
    z-index: 3000;
}
#replay{
    position: absolute;
    margin-top: 50px;
    left: 50%;
    transform: translateX(-50%);
    border:1px solid red;
    border-radius: 15px;
    background-color: red;
    color: white;
    font-size: 15px;
    cursor: pointer;
    width: 90px;
    height: 30px;
    z-index: 3000;
}
a{
    color: rgb(0, 0, 0);
    text-decoration: none;
}
a:hover{
    font-size: 20px;
    color: royalblue;
    text-decoration: none;
}
#dev{
    position: fixed;
    top: 90%;
    font-family: Arial, Helvetica, sans-serif;
    font-size: 17px;
    font-weight: 500;
    background-color: transparent;
}
#onWIN,#onWIN_M{
    visibility: hidden;
    text-align: center;
    position: absolute;
    z-index: 2000;
    height: 25%;
    margin-bottom: 3px;
    border-radius: 5px ;
    border: 1px solid black;
    width: 200px;
    background-color:rgb(14, 14, 14) ;
    margin-left: 50%;
    margin-right: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
}
#a,#nd2{

    margin-top: -15%;
    margin-left: 15%;
      color: black;
    font-weight: 300;
    font-family: Arial, Helvetica, sans-serif;
}

#nd2{
    margin-left: 23%;
}
img{
height: 40px;
width: 50px;
margin-left: -60%;

margin-top: 15%;
border-radius: 50px;
}
#s{
    font-size: 32px;
    font-family: Arial, Helvetica, sans-serif;
    background-color:rgb(29, 28, 20) ;
    padding-bottom: 3px;
    color: red;
    backface-visibility: hidden; 
    background-attachment: fixed;
    opacity: 1;
   
    background-color: transparent;
    border-bottom: 1px solid #adadad;
    border-bottom-width: -0.01px;
    width: 100%;
}